import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames/bind';

import { Flex, FlexItem, Badge, Heading, Text } from '..';

const cx = classNames.bind();

const Coupon = (props) => {
    const { badgeLabel, couponLabel, codeLabel, code, saving, expiry, footnote, showInfo, opacity } = props;

    const couponClass = cx([
        {
            [`opacity-${opacity}`]: opacity,
        },
    ]);

    const couponStyle = {
        visibility: opacity === '0' ? 'hidden' : null,
    };

    return (
        <div className={couponClass} style={couponStyle}>
            <Flex>
                <FlexItem flexGrow="0">
                    <Badge appearance="secondary" padding="2">
                        {badgeLabel && <Text label={badgeLabel} size="tiny" marginBottom="0" color="white" align="center" lineHeight="reset" />}
                        {saving && <Text label={saving} size="xlarge" marginBottom="0" color="white" align="center" lineHeight="reset" />}
                        {expiry && (
                            <Text label={expiry} size="tiny" marginBottom="0" color="white" align="center" textTransform="capitalize" lineHeight="reset" />
                        )}
                    </Badge>
                </FlexItem>
                {showInfo && (
                    <FlexItem flexGrow="2" paddingLeft="3" paddingVertical="2">
                        {couponLabel && <Heading label={couponLabel} size="small" marginBottom="2" />}
                        {footnote && (
                            <Text size="small" marginBottom="0" lineHeight="reset">
                                <span className="color-secondary">* </span>
                                <span>{footnote}</span>
                            </Text>
                        )}
                        <Text size="small" marginBottom="0" lineHeight="reset">
                            {codeLabel && (
                                <span>
                                    {codeLabel}
:
                                    {' '}
                                </span>
                            )}
                            {code && <strong>{code}</strong>}
                        </Text>
                    </FlexItem>
                )}
            </Flex>
        </div>
    );
};

Coupon.defaultProps = {
    badgeLabel: '',
    couponLabel: '',
    codeLabel: undefined,
    code: undefined,
    saving: undefined,
    expiry: undefined,
    footnote: undefined,
    showInfo: true,
    opacity: undefined,
};

Coupon.propTypes = {
    badgeLabel: PropTypes.string,
    couponLabel: PropTypes.string,
    codeLabel: PropTypes.string,
    code: PropTypes.string,
    saving: PropTypes.string,
    expiry: PropTypes.string,
    footnote: PropTypes.string,
    showInfo: PropTypes.bool,
    opacity: PropTypes.string,
};

export default Coupon;
